.Navbar{
  
    position: absolute;
    width:100%;
    height:5%;
    background-color: transparent;
    z-index:101;
}
.list-links button{
    border:none;
    background-color: transparent;
    color:white;
    outline: 0 !important;
}

.list ul{   
   padding: 0;
    top:0;
    left:0;
    box-sizing: border-box;
    display: flex;
    font-family: 'Oswald';
    justify-content: center;
    list-style-type: none;
}
.list-links a{
color:white;
}
.list-links {
    margin-right: 10px;
    margin-left: 10px;
    list-style-type: none;
    font-size: 22px;
    color: white;
    text-decoration: none;
}
.list-links a:hover{
    color:white;
    text-decoration: none;
    list-style-type: none;

}
.list-links:after{
    content: '';
    display:block;
    width:0;
    height:2px;
    background-color: #FFF;
    transition: width .3s ;

}

.list-links:hover:after {
    background-color: white;
    text-decoration: none;
    width:100%;
    transition: width .3s;


}


@media screen and (max-width: 750px){
    .list{
      display: flex;
      flex-direction: column;
      justify-content: center;
      align-items: center;
      overflow: hidden;
      flex:1;
  
    }

    .list-ul{
        margin-top: 10px;
        font-size: 20px;
    }
    .Navbar{
       
        position: absolute;
        width:100%;
        height:10%;
        background-color: transparent;
       
    }
    .underline{
        
        flex-direction: column;
        justify-content: center;
        height: 2px;
        width: 300px;
        background-image: linear-gradient(to right, rgba(255,0,0,0), white, transparent);
     
    }
}